* {
  font-family: 'Roboto', sans-serif;
}

.bigger-container {
  background-image: url(pexels-keith-wako-89909.jpg);
  background-size: cover;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-container {
  display: flex;
  justify-content: space-between;
}

.container {
  min-height: 100vh;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  color: white;
}

header {
  align-self: flex-start;
  min-width: 100%;
  display: flex;
  justify-content: space-around;
  min-height: 100px;
  align-items: center;
  background-color: #0A0A0A;
  color: white;
  margin-bottom: 30px;
}

.links {
  width: 70%;
  display: flex;
  justify-content: space-around;
}

.link {
  text-decoration: none;
  color: white;
  font-weight: bold;
}

h6 {
  color: white;
  font-weight: bold;
}

.link:hover {
  color: #F9D71C;
}

.search-bigger-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}

.search-input {
  max-width: 60%;
  display: flex;
  align-self: center;
  margin-top: 10px;
}

.search-words {
  align-self: center;
}

.search-results {
  align-self: center;
  border: 2px solid transparent;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  max-width: 1500px;
}

.album-card {
  width: 300px;
  background-color: rgba(10, 10, 10, 0.8);
  padding: 10px;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.album-card img {
  width: 100px;
  height: 100px;
}

.album-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.songs {
  /* border: 2px solid red; */
  display: flex;
  flex-direction: column;
  width: 800px;
  margin-bottom: 30px;
  background-color: rgba(10, 10, 10, 0.8);
  padding: 30px;
  margin-top: 50px;
  border-radius: 10px;
}

.song-card {
  /* border: 2px solid blue; */
  display: flex;
  width: 100%;
  justify-content: space-around;
  margin-top: 30px;
  align-items: center;
  flex-wrap: wrap;
}

.song-name {
  /* border: 2px solid blue; */
  display: flex;
  width: 250px;
  justify-content: center;
  align-items: center;
}

span {
  align-self: center;
}

.user-info {
  background-color: rgba(10, 10, 10, 0.8);
  padding: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}

.user-info img {
  width: 100px;
  height: 100px;
  border-radius: 100%;
  margin-bottom: 10px;
}

header span img {
  width: 30px;
  height: 30px;
  border-radius: 100%;
}

.loading {
  color: white;
}